.tns{width:100%;margin-top:10rem;padding:30px;position:relative}.tns .tnsinner{display:flex;justify-content:center;position:relative;max-width:1400px;width:87.5rem;margin:0 auto;position:relative}.tns .tnsinner h2{font-family:"양진체","Pretendard Variable";color:#fff;width:400px;padding:20px;font-size:2.4rem;margin-top:10rem;position:sticky}.tns .tnsinner .toolcontainer{padding-left:5rem;margin-top:6rem;display:grid;gap:2rem;grid-template-columns:1fr 1fr}.tns .tnsinner .toolcontainer .tools{border:1px solid #ccc;padding:4rem;display:flex;flex-direction:column;justify-content:space-between;grid-row:span 2}.tns .tnsinner .toolcontainer .tools:nth-child(2){grid-column:2/span 1;grid-row:2/span 2}.tns .tnsinner .toolcontainer .tools h3{font-size:2.5rem;color:#fff}.tns .tnsinner .toolcontainer .tools .toolBox{color:#a8a8a8}.tns .tnsinner .toolcontainer .tools .toolBox p{color:#fff;font-size:1.5rem}