.workList{width:100%;color:#fff;position:relative;margin-bottom:4rem}.workList .gridContainer{position:relative;width:100%;max-width:1400px;margin:0 auto}.workList .gridContainer ul{margin-top:4rem;gap:1rem;display:grid;grid-template-columns:repeat(3, 1fr)}.workList .gridContainer ul li{width:100%;height:100%;background-color:#fff;color:#000;cursor:pointer}.workList .gridContainer ul li a{width:100%;height:100%}.workList .gridContainer ul li figure{position:relative;overflow:hidden;text-align:center;display:inline-block;width:100%;height:100%;background-color:#223046}.workList .gridContainer ul li figure:hover img{opacity:.6;transform:scale3d(1, 1, 1)}.workList .gridContainer ul li figure:hover p{opacity:1;transition-delay:.1s}.workList .gridContainer ul li figure:hover figcaption::before{transform:scale3d(1.9, 1.4, 1) rotate3d(0, 0, 1, 45deg) translate3d(0, 100%, 0)}.workList .gridContainer ul li figure img{width:100%;height:100%;opacity:.95;transition:opacity .35s,transform .35s;transform:scale3d(1.05, 1.05, 1)}.workList .gridContainer ul li figure figcaption{position:absolute;top:0;left:0;width:100%;height:100%;backface-visibility:hidden}.workList .gridContainer ul li figure figcaption::before{content:"";position:absolute;top:0;left:0;width:100%;height:100%;background-color:rgba(16,161,157,.7);transition:transform .6s;transform:scale3d(1.9, 1.4, 1) rotate3d(0, 0, 1, 45deg) translate3d(0, -100%, 0)}.workList .gridContainer ul li figure figcaption p{color:#fff;position:absolute;right:0;bottom:0;margin:3em;padding-right:1rem;max-width:150px;border-right:4px solid #fff;text-align:right;opacity:0;transition:opacity .35s;z-index:999}.workList .gridContainer ul li figure figcaption a{z-index:1000;text-indent:200%;white-space:nowrap;font-size:0;opacity:0;width:100%;height:100%}.workList .gridContainer ul li figure figcaption h2{color:#fff;margin:3rem auto;font-size:2rem;font-family:"양진체","Pretendard Variable"}