html {
  overflow-y: scroll;
  height: 100%;
  -webkit-overflow-scrolling: touch;
}
body {
  overflow-y: visible;
  position: relative;
  overflow-x: hidden;
}
html,
body {
  margin: 0;
}

.container {
  width: 200vw;
  height: 100vh;
  display: flex;
  flex-wrap: wrap;
}

.panel {
  width: 90vw;
  height: 90vh;
}
.imgs {
  position: absolute;
  bottom: 100px;
  left: 110px;
}
.workOut {
  position: absolute;
  bottom: 50px;
  left: 150px;
  color: white;
  font-family: "양진체", "Pretendard Variable";
  font-size: 4rem;
}
.scrolling {
  white-space: nowrap;
  overflow: hidden;
  border: 2px solid #fff;
  border-radius: 50px;
  width: 900px;
  display: flex;
  position: absolute;
  bottom: -20px;
  left: 50px;
}
.scrollingText {
  font-weight: bold;
  font-size: 24px;
  color: rgb(255, 255, 255);
  animation: scrollingAnimation 3s linear infinite;
}
@keyframes scrollingAnimation {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-450px);
  }
}

.swiperContainer {
  margin-top: 3rem;
  display: flex;
  align-items: flex-start;
  width: 100vw;
}

.swiperWapper {
  font-family: "양진체", "Pretendard Variable";
  background-color: rgba(255, 255, 255, 0.5);
  margin-right: 40px;
  text-align: center;
  padding: 30px 40px;
  border: 1px solid #fff;
}
.boxInner {
  width: 650px;
  height: 600px;
  display: flex;
  text-align: center;
  margin: 50px auto;
}
.boxInner .hashBox {
  color: rgb(34, 34, 34);
  text-align: center;
  height: 30%;
}
.hashBox h3 {
  font-family: "양진체", "Pretendard Variable";
  font-size: 18px;
  display: inline-block;
  border: 2px solid #000;
  border-radius: 20px;
  padding: 3px 13px;
}
.hashBox h2 {
  font-size: 52px;
  margin-top: 15px;
  font-family: "양진체", "Pretendard Variable";
}

.hashTag {
  font-family: "KIMM_Bold", "Pretendard Variable";
  color: #555;
  display: flex;
  justify-content: center;
}
.hashTag li {
  margin: 4px;
  position: relative;
}
a {
  text-decoration: none;
}
.movePj {
  transition: all 0.5s;
}
.movePj:hover {
  color: #1d5c63;
}
.swiperImage {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.swiperImgWrap {
  height: 520px;
  border: 10px solid black;
  border-radius: 20px;
  overflow: hidden;
}

.swiperImage img {
  height: 100%;
}

.pjname {
  font-size: 18px;
  margin-bottom: 10px;
}

.moreBtnBox {
  position: relative;
  height: 95vh;
}
.moreBtnBox .moreBtn {
  font-family: "양진체", "Pretendard Variable";
  width: 200px;

  background-color: transparent;
  padding: 10px 20px;
  border-radius: 20px;
  border: 2px solid #fff;
  color: #10a19d;
  position: absolute;
  bottom: 50px;
  cursor: pointer;
  transition: all 0.5s;

  font-size: 1.4rem;
}
.moreBtnBox .moreBtn a {
  text-align: center;
}
.moreBtnBox .moreBtn:hover {
  background-color: #10a19d;
  border: 5px solid #fff;
  color: #fff;
}
